header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* styling cta */
.cta {
    margin-top: 2.5 rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* socials */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 10%;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* me  */
/* .me{
    width:22rem;
    height:36rem;
    position: absolute;
    overflow:hidden;
    padding:5 rem 1.5rem 1.5rem 1.5rem;
} */
.me {
    width: 42rem;
    height: 66rem;
    position: absolute;
    left: 20%;
    padding: 5rem 1.5rem 1.5rem 1, 5rem;
    margin-bottom: 10rem;
}

/* .me{
    width:25rem;
    height:100rem;
    position: absolute;
    left:35%;
    margin-top: 3rem;
} */
.scroll__down {
    position: absolute;
    right: 1rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width:1024px) {
    header {
        height: 68vh;

    }
}

@media screen and (max-width:600px) {
    header {
        height: 100vh;
    }

    .header__socials,
    .scroll__down {
        display: none;
    }
}